// RolesTable.tsx
import React from 'react';
import { Table, Button } from 'react-bootstrap';

export default function RolesTable({ roles, onNewRole }) {
  return (
    <div>
      <h4></h4>
      <Button className="mb-3" onClick={onNewRole}> </Button>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th></th>
            <th> </th>
            <th> </th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {roles.map((role, index) => (
            <tr key={role.id}>
              <td>{index + 1}</td>
              <td>{role.name}</td>
              <td>{role.createdAt}</td>
              <td>
                <Button size="sm" variant="primary" className="me-2"></Button>
                <Button size="sm" variant="danger"> </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
}
