// UsersTable.tsx
import React from 'react';
import { Table, Button } from 'react-bootstrap';

export default function UsersTable({ users, onNewUser }) {
  return (
    <div>
      <h4></h4>
      <Button className="mb-3" onClick={onNewUser}> </Button>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th></th>
            <th> </th>
            <th></th>
            <th> </th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {users.map((user, index) => (
            <tr key={user.id}>
              <td>{index + 1}</td>
              <td>{user.name}</td>
              <td>{user.email}</td>
              <td>{user.createdAt}</td>
              <td>
                <Button size="sm" variant="primary" className="me-2"></Button>
                <Button size="sm" variant="danger"> </Button>
              </td>
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
}
