import React, { useEffect, useRef, useState } from "react";
import { Table, Button, Form, InputGroup, FormControl, Spinner, Collapse } from "react-bootstrap";
import api from "../api/axios"; //    baseURL   

export default function GuideManager() {
  const [guides, setGuides] = useState([]);
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(false);
  const [fetchError, setFetchError] = useState("");

  //  
  const [selectedCategory, setSelectedCategory] = useState("");
  const [files, setFiles] = useState([]); //      
  const [createError, setCreateError] = useState("");
  const [createLoading, setCreateLoading] = useState(false);
  const fileInputRef = useRef(null);

  // 
  const [search, setSearch] = useState("");

  //  /  
  const [openCategories, setOpenCategories] = useState({});

  useEffect(() => {
    fetchGuides();
    fetchCategories();
  }, []);

  const fetchGuides = async () => {
    setLoading(true);
    setFetchError("");
    try {
      const res = await api.get("guide");
      setGuides(res.data || []);
    } catch (err) {
      console.error(err);
      setFetchError("   .");
    } finally {
      setLoading(false);
    }
  };

  const fetchCategories = async () => {
    try {
      const res = await api.get("guide/categories");
      setCategories(res.data || []);
    } catch (err) {
      console.error(err);
    }
  };

  //   
  const handleCreate = async (e) => {
  e.preventDefault();
  setCreateError("");

  if (!selectedCategory) {
    setCreateError("    .");
    return;
  }
  if (files.length === 0) {
    setCreateError("       .");
    return;
  }

  const formData = new FormData();
  formData.append("GuideCategoryId", selectedCategory);
  files.forEach(file => formData.append("Addresses", file)); //    

  setCreateLoading(true);
  try {
    await api.post("guide", formData, {
      headers: { "Content-Type": "multipart/form-data" }
    });

    setSelectedCategory("");
    setFiles([]);
    if (fileInputRef.current) fileInputRef.current.value = "";
    await fetchGuides();
  } catch (err) {
    console.error(err);
    setCreateError("   .");
  } finally {
    setCreateLoading(false);
  }
};


  // / 
  const toggleStatus = async (g) => {
    const id = g.id;
    try {
      if (g.isDeleted) {
        await api.post(`guide/activate/${id}`);
      } else {
        await api.post(`guide/unactivate/${id}`);
      }
      await fetchGuides();
    } catch (err) {
      console.error(err);
      alert("      .");
    }
  };

  //   
  const visible = guides.filter(g =>
    (g.guideCategoryName || "").toLowerCase().includes(search.trim().toLowerCase())
  );

  //     
  const groupedData = visible.reduce((acc, item) => {
    const key = item.guideCategoryName || " ";
    if (!acc[key]) acc[key] = [];
    acc[key].push(item);
    return acc;
  }, {});

  // /  
  const handleToggle = (category) => {
    setOpenCategories(prev => ({ ...prev, [category]: !prev[category] }));
  };

  return (
    <div className="container mt-3">
      <h4 className="mb-3">  </h4>
      <Form onSubmit={handleCreate}>
        <InputGroup className="mb-2" style={{ maxWidth: 500 }}>
          <Form.Select
            value={selectedCategory}
            onChange={e => setSelectedCategory(e.target.value)}
          >
            <option value=""> </option>
            {categories.map(c => (
              <option key={c.id} value={c.id}>{c.name}</option>
            ))}
          </Form.Select>
        </InputGroup>
        <InputGroup className="mb-2" style={{ maxWidth: 500 }}>
          <FormControl
            type="file"
            accept="audio/*"
            ref={fileInputRef}
            onChange={e => setFiles(Array.from(e.target.files))} //   
            multiple
          />
        </InputGroup>
        {createError && <div className="text-danger mb-2">{createError}</div>}
        <Button variant="success" type="submit" disabled={createLoading}>
          {createLoading ? <Spinner animation="border" size="sm" /> : ""}
        </Button>
      </Form>

      <hr />

      <div className="d-flex justify-content-between align-items-center mb-2">
        <h4> </h4>
        <Form.Control
          type="search"
          placeholder="..."
          value={search}
          onChange={e => setSearch(e.target.value)}
          style={{ maxWidth: 300 }}
        />
      </div>

      {fetchError ? (
        <div className="alert alert-danger">{fetchError}</div>
      ) : (
        Object.keys(groupedData).length === 0 ? (
          <div className="text-center">  </div>
        ) : (
          Object.keys(groupedData).map(category => (
            <div key={category} className="mb-3 border rounded">
              <Button
                variant="secondary"
                onClick={() => handleToggle(category)}
                className="w-100 text-end p-2"
              >
                {category} ({groupedData[category].length}) {openCategories[category] ? "" : ""}
              </Button>

              <Collapse in={openCategories[category]}>
                <div>
                  <Table striped bordered hover responsive className="mb-0">
                    <thead>
                      <tr>
                        <th></th>
                        <th> </th>
                        <th> </th>
                        <th></th>
                        <th></th>
                      </tr>
                    </thead>
                    <tbody>
                      {groupedData[category].map((g, i) => (
                        <tr key={g.id}>
                          <td>{i + 1}</td>
                          <td>
                            {g.address ? (
                              <audio controls style={{ width: 200 }}>
                                <source src={`https://totivar.com/${g.address}`} type="audio/mpeg" />
                                       .
                              </audio>
                            ) : (
                              "-"
                            )}
                          </td>
                          <td>{g.creationDate}</td>
                          <td>{g.isDeleted ? "" : ""}</td>
                          <td>
                            <Button
                              size="sm"
                              variant={g.isDeleted ? "success" : "danger"}
                              onClick={() => toggleStatus(g)}
                            >
                              {g.isDeleted ? " " : " "}
                            </Button>
                          </td>
                        </tr>
                      ))}
                    </tbody>
                  </Table>
                </div>
              </Collapse>
            </div>
          ))
        )
      )}
    </div>
  );
}
