import React, { useEffect, useState } from "react";
import { Table, Button, Form, Modal, Spinner, InputGroup, FormControl } from "react-bootstrap";
import api from "../api/axios"; //          baseURL 

export default function CategoriesManager() {
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(false);
  const [fetchError, setFetchError] = useState("");

  // create form
  const [newTitle, setNewTitle] = useState("");
  const [createError, setCreateError] = useState("");
  const [createLoading, setCreateLoading] = useState(false);

  // search filter
  const [search, setSearch] = useState("");

  // edit modal
  const [showEdit, setShowEdit] = useState(false);
  const [editId, setEditId] = useState(null);
  const [editTitle, setEditTitle] = useState("");
  const [editLoading, setEditLoading] = useState(false);
  const [editError, setEditError] = useState("");

  useEffect(() => {
    fetchCategories();
  }, []);

  const fetchCategories = async () => {
    setLoading(true);
    setFetchError("");
    try {
      const res = await api.get("category");
      setCategories(res.data || []);
    } catch (err) {
      console.error(err);
      setFetchError("   .");
    } finally {
      setLoading(false);
    }
  };

  // helper: normalize title for duplicate check
  const norm = (t) => (t || "").trim().toLowerCase();

  // create
  const handleCreate = async (e) => {
    e?.preventDefault();
    setCreateError("");
    const title = newTitle.trim();
    if (!title) {
      setCreateError("    .");
      return;
    }

    //    
    if (categories.some(c => norm(c.title) === norm(title))) {
      setCreateError("    .");
      return;
    }

    setCreateLoading(true);
    try {
      const res = await api.post("category", { title });
      // :       
      await fetchCategories();
      setNewTitle("");
    } catch (err) {
      console.error(err);
      setCreateError("   .");
    } finally {
      setCreateLoading(false);
    }
  };

  // open edit modal
  const openEdit = (cat) => {
    setEditId(cat.id);
    setEditTitle(cat.title || "");
    setEditError("");
    setShowEdit(true);
  };

  const closeEdit = () => {
    setShowEdit(false);
    setEditId(null);
    setEditTitle("");
    setEditError("");
  };

  // submit edit
  const handleEditSubmit = async (e) => {
    e?.preventDefault();
    setEditError("");
    const title = editTitle.trim();
    if (!title) {
      setEditError("    .");
      return;
    }

    //        (   )
    if (categories.some(c => c.id !== editId && norm(c.title) === norm(title))) {
      setEditError("       .");
      return;
    }

    setEditLoading(true);
    try {
      // EditCategory    Id  Title   ( Backend )
      const payload = { id: editId, title };
      const res = await api.post("category/edit", payload);
      //           
      await fetchCategories();
      closeEdit();
    } catch (err) {
      console.error(err);
      setEditError("   .");
    } finally {
      setEditLoading(false);
    }
  };

  // toggle activate / unactivate
  const toggleStatus = async (cat) => {
    // cat.isDeleted :  true =>    activate  
    const isCurrentlyDeleted = !!cat.isDeleted;
    const id = cat.id;
    try {
      //      
      if (isCurrentlyDeleted) {
        await api.post(`category/activate/${id}`);
      } else {
        await api.post(`category/unactivate/${id}`);
      }
      //  
      await fetchCategories();
    } catch (err) {
      console.error(err);
      alert("      .");
    }
  };

  // filtered list by search
  const visible = categories.filter(c =>
    (c.title || "").toLowerCase().includes(search.trim().toLowerCase())
  );

  return (
    <div className="container mt-3">
      <div className="row justify-content-center">
        <div className="col-md-6">
          <h4 className="mb-3">  </h4>
          <Form onSubmit={handleCreate}>
            <Form.Group>
              <Form.Label> </Form.Label>
              <InputGroup>
                <FormControl
                  placeholder=": "
                  value={newTitle}
                  onChange={(e) => setNewTitle(e.target.value)}
                />
                <Button variant="success" type="submit" disabled={createLoading}>
                  {createLoading ? <Spinner animation="border" size="sm" /> : ""}
                </Button>
              </InputGroup>
              {createError && <div className="text-danger mt-1">{createError}</div>}
            </Form.Group>
          </Form>
        </div>
      </div>

      <hr />

      <div className="row mb-2">
        <div className="col-md-6">
          <h4></h4>
        </div>
        <div className="col-md-6 d-flex justify-content-end align-items-center">
          <Form.Control
            type="search"
            placeholder="..."
            value={search}
            onChange={e => setSearch(e.target.value)}
            style={{ maxWidth: 300 }}
          />
        </div>
      </div>

      {loading ? (
        <div className="text-center my-4">
          <Spinner animation="border" />
        </div>
      ) : fetchError ? (
        <div className="alert alert-danger">{fetchError}</div>
      ) : (
        <Table striped bordered hover responsive>
          <thead>
            <tr>
              <th></th>
              <th> </th>
              <th> </th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {visible.length === 0 ? (
              <tr>
                <td colSpan="5" className="text-center">  </td>
              </tr>
            ) : (
              visible.map((cat, i) => (
                <tr key={cat.id}>
                  <td style={{ width: 80 }}>{i + 1}</td>
                  <td>{cat.title}</td>
                  <td>{cat.creationDate}</td>
                  <td>{cat.isDeleted ? "" : ""}</td>
                  <td style={{ width: 220 }}>
                    <Button size="sm" variant="primary" className="me-2" onClick={() => openEdit(cat)}>
                      
                    </Button>
                    <Button
                      size="sm"
                      variant={cat.isDeleted ? "success" : "danger"}
                      onClick={() => toggleStatus(cat)}
                    >
                      {cat.isDeleted ? " " : " "}
                    </Button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </Table>
      )}

      {/* Edit Modal */}
      <Modal show={showEdit} onHide={closeEdit} centered>
        <Form onSubmit={handleEditSubmit}>
          <Modal.Header closeButton>
            <Modal.Title> </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <Form.Group>
              <Form.Label></Form.Label>
              <Form.Control
                value={editTitle}
                onChange={(e) => setEditTitle(e.target.value)}
                placeholder=" "
                autoFocus
              />
              {editError && <div className="text-danger mt-1">{editError}</div>}
            </Form.Group>
          </Modal.Body>
          <Modal.Footer>
            <Button variant="secondary" onClick={closeEdit}></Button>
            <Button variant="primary" type="submit" disabled={editLoading}>
              {editLoading ? <Spinner animation="border" size="sm" /> : " "}
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    </div>
  );
}
